import React from 'react';
import { Box, Button, Typography } from '@mui/material';
import { useHistory } from 'react-router-dom';
import backgroundImage from '../../img/back4.jpeg';

const Main = () => {
  const history = useHistory();

  return (
    <Box
      sx={{
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'flex-start',
        color: 'white',
        textAlign: 'center',
        paddingTop: '10px',
        position: 'relative',
      }}
    >
      <Typography variant="h3" gutterBottom>
        Banana Brain Busters
      </Typography>
      <Button
        variant="contained"
        sx={{
          backgroundColor: '#ff9800', 
          color: 'white', 
          padding: '10px 30px',
          fontSize: '1.5rem',
          borderRadius: '8px',
          boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.3)', 
          position: 'absolute',
          bottom: '550px',
          left: '50%',
          transform: 'translateX(-50%)',
          transition: 'all 0.3s ease-in-out', // Smooth transition for hover effects
          '&:hover': {
            backgroundColor: '#e68900', // Darker orange on hover
            boxShadow: '0px 6px 15px rgba(0, 0, 0, 0.4)', // Stronger shadow on hover
            transform: 'translateX(-50%) scale(1.1)', // Slight zoom on hover
          },
        }}
        onClick={() => {
          history.push('/signin');
        }}
      >
        Play Now
      </Button>
    </Box>
  );
};

export default Main;
